/**
 * Ëé∑ÂèñÊâÄÊúâÁæéÈ£ü‰∫ßÂìÅÂàÜÁ±ª
 * @returns {Array} ÂàÜÁ±ªÂàóË°®
 */
export function getFoodCategories() {
  return foodCategories
}

/**
 * Ëé∑ÂèñÁæéÈ£ü‰∫ßÂìÅÂàóË°®
 * @param {string} categoryId - ÂèØÈÄâÁöÑÂàÜÁ±ªID
 * @returns {Array} ‰∫ßÂìÅÂàóË°®
 */
export function getFoodProducts(categoryId) {
  if (!categoryId || categoryId === '') {
    return sampleFoodProducts
  }
  return sampleFoodProducts.filter(product => product.categoryId === categoryId)
}

/**
 * Ëé∑ÂèñÂçï‰∏™ÁæéÈ£ü‰∫ßÂìÅËØ¶ÊÉÖ
 * @param {string} productId - ‰∫ßÂìÅID
 * @returns {Object|null} ‰∫ßÂìÅÂØπË±°Êàñnull
 */
export function getFoodProductById(productId) {
  if (!productId) {
    return null
  }
  return sampleFoodProducts.find(product => product.id === productId) || null
}

/**
 * Ëé∑ÂèñÂ§ñÈÉ®ÂïÜÂüéÈìæÊé•
 * @param {string} productId - ‰∫ßÂìÅID
 * @returns {string|null} ÂïÜÂüéÈìæÊé•Êàñnull
 */
export function getFoodShopUrl(productId) {
  const product = getFoodProductById(productId)
  if (!product) {
    return null
  }
  return product.shopUrl || null
}

export default {
  foodCategories,
  sampleFoodProducts,
  getFoodCategories,
  getFoodProducts,
  getFoodProductById,
  getFoodShopUrl
}
